}
.imgc{
  background: url(/static/media/international1.3dc021e1.jpg) no-repeat;
  background-size: cover;
  background-position: center;


}


.cardBottom {
  height: 35%;
  /* text-wrap: normal; */
  transition: 0.4s all ease;
  background: black;

  color: white;
}

.cardText {
  min-height: 100%;
  padding: 20px;

}

.cardTitle {
  font-size: 24px;
  font-weight: 700;
}

.cardInfo {
  padding-top: 10px;
  font-size: 18px;
  font-weight: 500;
  color: orange;
}

/* Hidding the entire so anything we put in will only show on hover */

.cardHoverText {
  visibility: hidden;
  font-family: 'Big Shoulders Inline Display', cursive;
  font-family: 'Open Sans Condensed', sans-serif;
}

/* Hover affects */

.card:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
}

/* Using className + hover followed by child element allows you to change child elements when parent is hovered */

.card:hover .cardHoverText {
  visibility: visible;
  transition: 0.4s all ease;
  transition-delay: 0.1999s;
}

.card:hover .cardBottom {
  transition: 0.4s all ease;
  height: 65%;
  opacity: 1;
}

.card:hover .cardTop {
  height: 35%;
  opacity: 0.8;
  transition: 0.4s all ease;
}

